.input_field {
	border-radius: 4px;
	margin-right: 24px;
	width: 48px;
	height: 48px;
}

/* List */
.contactList {
	background-color: #fff;
	border-radius: 4px;
	padding: 0 16px;
}

.contactListItem {
	border-bottom: 1px solid var(--border-color-1);
	background: var(--bg-white);
	cursor: pointer;
	display: flex;
	align-items: center;
	padding: 16px 0;
}

.contactListItem:last-of-type {
	border: none;
}

.contactListInitials {
	background-color: #F6F7F9;
	border-radius: 4px;
	font-size: 14px;
	font-weight: 500;
	width: 32px;
	height: 32px;
}

.contactListName {
	color: #141414;
	margin-right: auto;
}

.locationDetails {
	padding-left: 16px;
}